.dialogsItem {
  padding: 1rem;
  cursor: pointer;
}

.dialogsItem.active {
  font-size: 1.3rem;
  color: #585858;
  font-weight: 700;
}

.dialogsItem:hover {
  background-color: #f2f2f2;
}

.dialogsItem a {
  color: currentColor;
  text-decoration: none;
  display: inline-block;
  width: 80%;
}