h2 {
  margin: 0;
  color: #696969;
}

.dialogs {
  display: grid;
  gap: 1rem;
  grid-template-columns: 3fr 9fr;
  color: #838383;
}

.dialogsContent {
  margin-inline: 1rem;
}

.dialog {
  padding-right: 1rem;
  border-right: 2px solid #9e9d9d;
}

.dialogsList {
  padding: 0;
  margin: 0;
  margin-top: 1rem;
  list-style-position: inside;
}


.chat > * + * {
  margin-top: 2rem;
}


.chatInteractive {
  display: flex;
  padding: 1rem;
  gap: .5rem;
  border-radius: .5rem;
  background-color: #cccccc;
}

.chatTextArea {
  flex: 1;
  border: 0;
  resize: none;
  border-radius: .5rem;
  font: inherit;
  padding: .25rem;
  height: 2rem;
}

.chatSendMsgBtn {
  border: 0;
  background-color: wheat;
  padding: .5em 1em;
  border-radius: .25rem;
  cursor: pointer;
}