.chatMsg {
  display: flex;
  align-items: center;
  gap: 2rem;
}

.chatMsg.responsed {
  flex-direction: row-reverse;
}

.chatMsg + .chatMsg {
  margin-top: 2rem;
}

.chatUser {
  text-align: center;
}



.chatMsgField {
  position: relative;
  max-width: 36ch;
  padding: 1rem;
  border: 1px solid #838383;
  border-radius: .5rem;
}

.chatMsgField::after {
  content: '';
  /* width: 25px;
  height: 25px;
  border: 1px solid;
  position: absolute;
  top: 0;
  left: -1.6rem;
  clip-path: polygon(100% 0, 0 50%, 100% 100%);
  background-color: #bebebe; */

  width: 0;
  height: 0;
  
  position: absolute;
  border-bottom: solid 20px transparent;
  border-top: solid 20px transparent;
  border-left: solid 20px transparent;
  border-right: solid 20px #ebebeb;
  top: 0;
  left: -2.6rem;
}

.responsed .chatMsgField::after {
  left: initial;
  right: -2.6rem;
  border-left: solid 20px #ebebeb;
  border-right: solid 20px transparent;
}